import React from "react";
import { IDraggableComponent } from "../../Interfaces/IDraggableComponent";
import "./DraggableComponent.css";
interface IState {}
export default class DraggableComponent extends React.Component<
  IDraggableComponent,
  IState
> {
  render() {
    return (
      <React.Fragment>
        <div
          className="draggable-component"
          draggable={true}
          onDragStart={ev => {
            console.log(888);
            this.props.onDragStart(ev, this.props.name, this.props.type);
          }}
        >
          {this.props.name}
        </div>
      </React.Fragment>
    );
  }
}
